import React, {useEffect, useState} from 'react';
import styled from "styled-components";
import {BASE_URL, getHeaders, mainColor} from "../../../utills/ServiceUrls";
import {AiOutlineUnorderedList} from "react-icons/ai";
import {Card, CardContent} from "@mui/material";
import {DataGrid, GridToolbar} from "@mui/x-data-grid";
import axios from "axios";

function ArxivStudentsForRektor() {

    const columns = [
        {field: 'id', headerName: 'ID', width: 40, editable: false},
        {field: 'fullName', headerName: 'Full Name', width: 400, editable: false,},
        {field: 'groupName', headerName: 'Group', width: 100, editable: false,},
        {field: 'passport', headerName: 'Passport', width: 150, editable: false},
        {field: 'login', headerName: 'Login', width: 200, editable: false},
        {field: 'rfid', headerName: 'RFID', width: 200, editable: false},
        {field: 'rektororder', headerName: 'Rektors order', width: 150, editable: false},
        {field: 'teachStatus', headerName: 'Status', width: 180, editable: false,},

    ];
    const [data, setData] = useState([]);

    useEffect(() => {

        axios.get(BASE_URL + '/student/getStudentDataForTeachStatusAll')
            .then(res => {
                console.log(res.data)
                setData(res.data)
            })
            .catch(err => {
                console.log(err)
            })


    }, []);


    return (
        <Container>
            <Title>
                <AiOutlineUnorderedList/> Archive
            </Title>
            <Card sx={{mt: 3}}>
                <CardContent>
                    <DataGrid
                        checkboxSelection
                        style={{width: "100%", minHeight: "300px!important"}}
                        columns={columns}
                        rows={data}
                        components={{Toolbar: GridToolbar}}/*** print and excel ****/
                        autoHeight
                        pageSize={20}
                        initialState={{ // hide items
                            ...data.initialState,
                            columns: {
                                columnVisibilityModel: {
                                    id: false,
                                    cardNo: false,
                                    login: false,
                                    email: false,
                                    passport: false,
                                    rektororder: false,
                                    rfid: false,
                                },
                            },
                        }}
                    />
                </CardContent>
            </Card>
        </Container>
    );
}

const Title = styled.div`
    font-weight: bold;
    font-size: 25px;
    color: ${mainColor};
`
const Container = styled.div`
    width: 100%;
    padding: 1rem;
`
export default ArxivStudentsForRektor;